/*My Custom CSS*/
/*Header or NavBar*/
.header {
    position: fixed;
    top: 0;
    z-index: 1;
    width: 100%;
    background-color:  #262626;
}

.header h3{
    font-weight: bold;
    font-size:1.4em;
    display: inline-block;
    margin-left:15px;
    margin-right:15px;
}
@media (max-width: 737px){
    .header h3 {
        display: none;
    }
}

/*Anchor Link*/
.anchorLink{
    background: -webkit-linear-gradient(45deg,#c39d63, #fad8a0);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-transition: background 2s ease-out 1s;
    transition: background 2s ease-out 1s;
}
.anchorLink:hover{
    background: -webkit-linear-gradient(45deg,#fff, #fff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* The progress container (grey background) */
.progress-container {
    width: 100%;
    height: 4px;
    background: #1a1a1a;
}

/* The progress bar (scroll indicator) */
.progress-bar {
    height: 4px;
    background: -webkit-linear-gradient(0.75turn,#c39d63, #fad8a0);
    width: 0%;
}

/*Introduction*/
.introduction{
    padding-top:12vh;
    min-height:100vh;
}
.myName {
    text-align:left;
    font-size:15em;
    cursor: default;
}
.myName p{
    padding-left: 1.5vw;
    font-size:10%;
    color:#fad8a0;
    letter-spacing: 0.5em;
    margin: 0;
}
.myName h1{
    font-size: 100%;
    line-height: 0.9em;
    -webkit-transition: 1s;
    transition: 1s;
    color: #262626;
}
.myName h1:hover {
    color: white;
    -webkit-transition: 1.5s;
    transition: 1.5s;
    -webkit-transform: translate(5px);
            transform: translate(5px);
}
/*Animated Paragraph*/
.myName h3{
    color:white;
    font-family: 'Raleway', sans-serif;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid #fad8a0; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    font-size:35px;
    width:56%;
    margin-top:0; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em; /* Adjust as needed */
    -webkit-transition: 1s;
    transition: 1s;
    -webkit-animation:
            typing 2.5s steps(30, end),
            blink-caret 1s step-end infinite;
            animation:
            typing 2.5s steps(30, end),
            blink-caret 1s step-end infinite;
}

@media(max-width: 737px){
    .myName {
        padding-top:2vh;
        text-align:center;
        font-size:5em;
        cursor: default;
    }
    .myName h1{
        color:#f2f2f2;
        line-height: 20vh;
    }
    .myName h1:hover{
        color:  #fad8a0;
        -webkit-transform: translate(0, -5px);
                transform: translate(0, -5px);
    }

    .myName p{
        font-size:25%;
    }
    .myName h3{
        margin-left:auto;
        margin-right:auto;
        font-size:14px;
        width:73vh;
        max-width:75%;
    }
}
/* The typing effect */
@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 60% }
}
@keyframes typing {
    from { width: 0 }
    to { width: 60% }
}
/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #fad8a0 }
}
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #fad8a0 }
}

