/*Fake Anchor*/
.anchor {
    display: block;
    height: 85px;
    margin-top: -85px;
    visibility: hidden;
}
/*Card CSS*/
.card{
    margin-top:10%;
    border-radius: 15px;
    height: 500px;
    width:32rem;
    /*Shadow effect*/
    box-shadow: 0px 15px 25px -6px rgba(196,196,196,1);
}
.card-pic{
    border-radius: 15px;
    height:350px;
    background-size:cover;
    background-position: center center;
    -webkit-transition: 1s;
    transition: 1s;
}
.card-pic:hover{
    border-radius:25px;
}

.card-desc{
    height:170px;
}
.card-desc h2{
    color: #c39d63;
    -webkit-transition: 500ms;
    transition: 500ms;
}
.card-desc h2:hover{
    color: #fad8a0;
}


.card-horizontal{
    display: inline-flex;
    text-align:center;
}


.card-content {
    position: relative;
    height: 300px; width:30vw;
    text-align:center;
    padding: 5% 7% 10px 18%;
    margin: 0 auto 50px -50px;
    border: 4px solid transparent;
    border-radius:25px;
    background-clip: padding-box;
}

.card-content::after {
    position: absolute;
    top: -4px; bottom: -4px;
    left: -4px; right: -4px;
    background-color: #262626;
    content: '';
    z-index: -1;
    border-radius: 25px;
}
.card-content p{
    text-align: justify;
    text-justify: inter-word;
    font-size:1.2em;
    font-family: 'Raleway', serif;
    color:white;
    letter-spacing: 0.08em;
}




